import 'ScssComponents/pet-step-form.scss';
import 'ScssComponents/pet-food-subscription-step-4.scss';
import * as noUiSlider from 'nouislider';
import 'nouislider/dist/nouislider.css';

const petFoodSubscriptionStep4 = () => {
	const valuesSlider = document.querySelector('.js-pet_food__slider');
	const valuesTitle = document.querySelector('.pet_food__slider_input_title');
	const valuesForSlider = ['< 1', 2, 4, 6, 8, 10, '12 +'];

	if (valuesSlider && valuesTitle) {
		const format = {
			to: function toVal(value) {
				return valuesForSlider[Math.round(value)];
			},
			from: function fromVal(value) {
				return valuesForSlider.indexOf(Number(value));
			},
		};

		noUiSlider.create(valuesSlider, {
			start: [1],
			// A linear range from 0 to 15 (16 values)
			range: { min: 0, max: valuesForSlider.length - 1 },
			// steps of 1
			connect: 'lower',
			step: 1,
			format: format, // eslint-disable-line
			pips: { mode: 'steps', format: format }, // eslint-disable-line
		});
		// The display values can be used to control the slider
		valuesSlider.noUiSlider.set(['1']);

		valuesSlider.noUiSlider.on('update', function (values, handle) { // eslint-disable-line
			valuesTitle.value = values[handle] + ' years old';// eslint-disable-line
		});
	}
};

export default petFoodSubscriptionStep4;
